export default {
	props: {
		// 本月或者本日会子详情
		huiziData: {
			type: Array,
			default: () => []
		},
	},
	data() {
		return {
			isHaveData:false,
		};
	},
	mounted(){
		this.isHaveData =  this.huiziData.length == 0? true:false;
	},
	watch:{
		huiziData( newdata ){
			this.isHaveData =  newdata.length == 0? true:false;
		}
		
	}
}
